<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chart.vue'
export default {
    data() {
        return {
            cdata: {
                colors: ['#50BA89', '#F5B51C'],
                height: '300px',
                width: '500px',
                legendData: ['供应商数量', '增长率'],
                legendShow: true,
                grid: {
                    top: '15%',
                    right: '10%',
                    left: '15%',
                    bottom: '20%',
                },
                xAxisData: [
                    '2013年',
                    '2014年',
                    '2015年',
                    '2016年',
                    '2017年',
                    '2018年',
                    '2019年',
                    '2020年',
                    '2021年',
                    '2022年',
                ],
                yAxisData: [
                    {
                        type: 'value',
                        name: '',
                        position: 'left',
                        alignTicks: true,
                        offset: 0,
                        axisLine: {
                            show: false,
                            lineStyle: {
                                color: 'rgb(46,80,149, 0.3)',
                            },
                        },
                        axisLabel: {
                            formatter: '{value}',
                            show: true,
                            textStyle: {
                                color: '#71788C',
                            },
                        },
                        splitLine: {
                            show: true, //网格线
                            lineStyle: {
                                color: ['rgb(46,80,149, 0.3)'],
                                width: 1,
                                type: 'solid',
                            },
                        },
                    },
                    {
                        type: 'value',
                        name: '',
                        position: 'right',
                        alignTicks: true,
                        offset: 0,
                        axisLine: {
                            show: false,
                        },
                        axisLabel: {
                            formatter: '{value}%',
                            show: true,
                            textStyle: {
                                color: '#71788C',
                            },
                        },
                    },
                ],
                seriesData: [
                    {
                        name: '供应商数量',
                        type: 'bar',
                        barWidth: '20px',
                        yAxisIndex: 0,
                        data: [200, 490, 700, 620, 500, 800, 600, 400, 200, 100],
                    },
                    {
                        name: '增长率',
                        type: 'line',
                        yAxisIndex: 1,
                        data: [30, 60, 90, 65, 53, 20, 16, 30, 20, 10],
                    },
                ],
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 10) - 1
                let tmp2 = Math.ceil(Math.random() * 10) - 1
                if (tmp1 >= 0 && tmp2 >= 0) {
                    let cdata = JSON.parse(JSON.stringify(this.cdata))
                    let num = cdata.seriesData[0].data[tmp1]
                    num = num + 30
                    cdata.seriesData[0].data[tmp1] = num

                    let num2 = cdata.seriesData[1].data[tmp2]
                    num2 = num2 + 5
                    cdata.seriesData[1].data[tmp2] = num2
                    this.cdata = cdata
                }
            }, 2000)
        },
    },
}
</script>

