<template>
    <Echart
        id="centerMap"
        ref="centreMapRef"
        :options="options"
        height="300px"
        width="500px"
    ></Echart>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {},
            geoCoordMap: {
                吉林长春133厂: [125.329155457, 43.81571024809454, 20],
                '沈阳410厂、606所': [123.432790922, 41.8086447835, 20],
                '北京资产公司、北京公司、503厂、621所、研究院': [116.395645038, 39.9299857781, 20],
                '哈尔滨120厂、哈轴': [126.657716855, 45.7732246332, 20],
                '陕西西安430厂、113厂': [108.953098279, 34.2777998978, 20],
                南京370厂: [119.798972,32.075667, 20],
            },
        }
    },
    components: {
        Echart,
    },
    props: {
        cdata: {
            type: Array,
            default: () => [],
        },
    },
    beforeDestroy() {
        clearInterval(this.intervalId)
    },
    watch: {
        cdata: {
            handler() {
                // 设置点的位置(经纬度)
                this.options = {
                    showLegendSymbol: true,
                    tooltip: {
                        show: false,
                        trigger: 'item',
                        textStyle: {
                            fontSize: 14,
                            lineHeight: 30,
                        },
                        backgroundColor: 'rgb(10,108,152, 0.7)',
                        position: point => {
                            // 固定在顶部
                            return [point[0] + 50, point[1] - 20]
                        },
                        // 如果需要自定义 tooltip样式，需要使用formatter
                        formatter: '{b}',
                    },
                    visualMap: {
                        min: 0,
                        max: 10,
                        show: false,
                        seriesIndex: 0,
                        // 颜色
                        inRange: {
                            color: ['rgba(41,166,206, .5)', 'rgba(69,117,245, .9)'],
                        },
                    },
                    // 底部背景
                    geo: {
                        show: true,
                        aspectScale: 0.8, //长宽比
                        zoom: 1.1,
                        // top: '10%',
                        left: '15%',
                        map: 'china',
                        roam: false,
                        itemStyle: {
                            normal: {
                                areaColor: 'rgba(0,0,0,0)',
                                shadowColor: 'rgba(7,114,204, .8)',
                                shadowOffsetX: 5,
                                shadowOffsetY: 5,
                            },
                            emphasis: {
                                areaColor: '#00aeef',
                            },
                        },
                    },
                    series: [
                        {
                            name: '相关数据',
                            type: 'map',
                            aspectScale: 0.8, //长宽比
                            zoom: 1.1,
                            mapType: 'china', // 自定义扩展图表类型
                            // top: '10%',
                            left: '15%',
                            itemStyle: {
                                normal: {
                                    color: 'red',
                                    areaColor: 'rgba(19,54,162, .5)',
                                    borderColor: 'rgba(0,242,252,.3)',
                                    borderWidth: 1,
                                    shadowBlur: 7,
                                    shadowColor: '#00f2fc',
                                },
                                emphasis: {
                                    areaColor: '#4f7fff',
                                    borderColor: 'rgba(0,242,252,.6)',
                                    borderWidth: 2,
                                    shadowBlur: 10,
                                    shadowColor: '#00f2fc',
                                },
                            },
                            label: {
                                formatter: params => `${params.name}`,
                                show: false,
                                position: 'insideRight',
                                textStyle: {
                                    fontSize: 14,
                                    color: '#efefef',
                                },
                                emphasis: {
                                    textStyle: {
                                        color: '#fff',
                                    },
                                },
                            },
                            nameMap: {
                                吉林: '吉林长春133厂',
                                辽宁: '沈阳410厂、606所',
                                黑龙江: '哈尔滨120厂、哈轴',
                                北京: '北京资产公司、北京公司、503厂、621所、研究院',
                                陕西: '陕西西安430厂、113厂',
                                江苏: '南京370厂',
                            },
                        },
                        {
                            type: 'scatter',
                            coordinateSystem: 'geo',
                            data: this.convertData([
                                {
                                    name: '吉林长春133厂',
                                    value: 110,
                                    position: 'insideTopRight',
                                },
                                {
                                    name: '沈阳410厂、606所',
                                    value: 110,
                                    position: 'insideTopLeft',
                                },
                                {
                                    name: '北京资产公司、北京公司、503厂、621所、研究院',
                                    value: 110,
                                    position: 'insideTopRight',
                                },
                                {
                                    name: '陕西西安430厂、113厂',
                                    value: 210,
                                    position: 'insideTopRight',
                                },
                                {
                                    name: '南京370厂',
                                    value: 210,
                                    position: 'insideTopLeft',
                                },
                                {
                                    name: '哈尔滨120厂、哈轴',
                                    value: 210,
                                    position: 'insideTopLeft',
                                },
                            ]),
                            zlevel: 12,
                            symbolSize: [5, 5], // 标记的大小,[宽、高]
                            // symbol: 'diamond', //气泡
                            // symbolOffset: [0, '-50%'], // 标记相对于原本位置的偏移
                            color: '#F5B51C',
                            label: {
                                normal: {
                                    show: false,
                                    // offset: [30, -10], // 是否对文字进行偏移。默认不偏移。例如：[30, 40] 表示文字在横向上偏移 30，纵向上偏移 40。
                                    formatter: function(params) {
                                        // console.log('气泡：', params)
                                        return '{fline|' + params.data.name + '}'
                                    },
                                    // position: 'insideTopLeft', // 标签相对于图形包围盒左上角的位置。
                                    distance: 0,
                                    backgroundColor: 'rgb(11, 108, 154, 1)',
                                    padding: [5, 5],
                                    borderRadius: 3,
                                    // verticalAlign:'middle',
                                    // lineHeight: 32,
                                    color: '#ffffff',
                                    rich: {
                                        fline: {
                                            padding: [4, 6, 4, 6],
                                            color: '#ffffff',
                                            // fontSize: 14
                                        },
                                    },
                                },
                                emphasis: {
                                    show: false,
                                },
                            },
                            itemStyle: {
                                emphasis: {
                                    borderColor: '#fff',
                                    borderWidth: 1,
                                },
                            },
                        },
                    ],
                }
            },
            immediate: true,
            deep: true,
        },
    },
    methods: {
        convertData(data) {
            var res = []
            var that = this
            const leftSymbol = 'circle'
            const rightSymbol = 'circle'
            for (var i = 0; i < data.length; i++) {
                var geoCoord = that.geoCoordMap[data[i].name]
                // console.log('geoCoord', geoCoord)
                if (geoCoord) {
                    res.push({
                        name: data[i].name,
                        value: geoCoord.concat(data[i].value),
                        label: {
                            normal: { position: data[i].position, offset: data[i].position == 'insideTopLeft' ? [30, -10]: [-30, -10] },
                        },
                        symbol: data[i].position == 'insideTopLeft' ? rightSymbol : leftSymbol,
                    })
                }
            }
            return res
        },
    },
}
</script>
