<template>
    <Echart :options="options" height="280px" width="500px"></Echart>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {
                color: ['#50BA89', '#20A0DC', '#2151A3', '#F5B51C', '#41BEE4'],
                legend: {
                    top: 'bottom',
                    left: 'right',
                    width: '120',
                    icon: 'square',
                    itemWidth: 5,
                    itemHeight: 5,
                },
                series: [
                    {
                        type: 'pie',
                        radius: [25, 120],
                        center: ['45%', '45%'],
                        roseType: 'area',
                        itemStyle: {
                            borderRadius: 8,
                        },
                        label: {
                            show: true,
                            normal: {
                                formatter: '{c}%',
                                position: 'inside',
                            },
                        },
                        emphasis: {
                            label: {
                                show: true,
                            },
                        },
                        data: [
                            { value: 30, name: '湖北省' },
                            { value: 35, name: '湖南省' },
                            { value: 40, name: '北京市' },
                            { value: 30, name: '四川省' },
                            { value: 28, name: '其它' },
                        ],
                    },
                ],
            },
        }
    },
    components: {
        Echart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 5) - 1
                if (tmp1 >= 0) {
                    let tmp2 = tmp1 == 0 ? 4 : tmp1 - 1
                    let options = JSON.parse(JSON.stringify(this.options))
                    let num = options.series[0].data[tmp1].value
                    let num2 = options.series[0].data[tmp2].value
                    options.series[0].data[tmp1].value = num + 1
                    options.series[0].data[tmp2].value = num2 - 1

                    this.options = options
                }
            }, 2000)
        },
    },
}
</script>

<style lang="scss" scoped></style>
