<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chart.vue'
export default {
    data() {
        return {
            cdata: {
                height: '300px',
                width: '500px',
                legendData: ['余额'],
                xAxisData: ['单位1','单位2', '单位3','单位4', '单位5', '单位6'],
                yAxisData: [
                    {
                        type: 'value',
                        name: '',
                        position: 'left',
                        alignTicks: true,
                        axisLine: {
                            show: false,
                        },
                        axisLabel: {
                            formatter: '{value}',
                            show: true,
                            textStyle: {
                                color: '#71788C',
                            },
                        },
                        splitLine: {
                            show: true, //网格线
                            lineStyle: {
                                color: ['rgb(46,80,149, 0.3)'],
                                width: 1,
                                type: 'solid',
                            },
                        },
                    },
                    
                ],
                seriesData: [
                    {
                        name: '余额',
                        type: 'bar',
                        barWidth : 18,
                        data: [9000, 7000, 5000, 3000, 1000, 500],
                    }
                ],
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 6) - 1
                if (tmp1 >= 0) {
                    let cdata = JSON.parse(JSON.stringify(this.cdata))
                    let num = cdata.seriesData[0].data[tmp1]
                    num = num + 500
                    cdata.seriesData[0].data[tmp1] = num
                    this.cdata = cdata
                }
            }, 2000)
        },
    },
}
</script>

<style lang="scss" scoped></style>
