<template>
    <Echart :options="options" height="300px" width="500px"></Echart>
</template>

<script>
import Echart from '@/common/echart'

export default {
    data() {
        return {
            options: {
                color: ['#50BA89', '#F5B51C', '#69B4D7'],
                polar: {
                    radius: [0, '90%'],
                },
                angleAxis: {
                    max: 100,
                    startAngle: 180,
                    axisLabel: { show: false },
                    axisLine: { show: false },
                },
                radiusAxis: {
                    type: 'category',
                    data: ['占比'],
                    axisLabel: { show: false },
                    axisLine: { show: false },
                },
                legend: {
                    show: true,
                    data: ['制造商', '服务商', '经销商'],
                    bottom: '30',
                    right: '0',
                    width: '100',
                    icon: 'square',
                    itemWidth: 5,
                    itemHeight: 5,
                    formatter: '{name}'
                },
                tooltip: {
                    formatter: '{a}: {c}%',
                },
                series: [
                    {
                        type: 'bar',
                        name: '制造商',
                        showBackground: true,
                        barWidth: '10px',
                        barGap: '100%',
                        barCategoryGap: '100%',
                        data: [20],
                        coordinateSystem: 'polar',
                    },
                    {
                        type: 'bar',
                        name: '服务商',
                        showBackground: true,
                        barWidth: '10px',
                        barGap: '100%',
                        barCategoryGap: '100%',
                        data: [60],
                        coordinateSystem: 'polar',
                    },
                    {
                        name: '经销商',
                        type: 'bar',
                        showBackground: true,
                        barWidth: '10px',
                        barGap: '100%',
                        barCategoryGap: '100%',
                        data: [80],
                        coordinateSystem: 'polar',
                    },
                ],
            },
        }
    },
    components: {
        Echart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 3) - 1
                let tmp2 = Math.ceil(Math.random() * 2) - 1
                if (tmp1 >= 0 && tmp2 >= 0) {
                    let options = JSON.parse(JSON.stringify(this.options))
                    let num = options.series[tmp1].data[0]
                    if (num >= 85) {
                        options.series[tmp1].data[0] = num - 10
                    } else if (num <= 15) {
                        options.series[tmp1].data[0] = num + 10;
                    } else {
                        options.series[tmp1].data[0] = tmp2 == 0 ? num -5 : num + 5;
                    }
                    
                    this.options = options
                }
            }, 3000)
        },
    },
}
</script>

<style lang="scss" scoped></style>
