<template>
    <Chart :cdata="cdata" />
</template>

<script>
import Chart from './chart.vue'
export default {
    data() {
        return {
            cdata: {
                height: '300px',
                width: '500px',
                legendData: ['成本'],
                xAxisData: ['A', 'B', 'C', 'D', 'E', 'F', 'G', 'H', 'I', 'J'],
                yAxisData: [
                    {
                        type: 'value',
                        name: '',
                        position: 'left',
                        alignTicks: true,
                        offset: 0,
                        axisLine: {
                            show: false,
                        },
                        axisLabel: {
                            formatter: '{value}',
                            show: true,
                            textStyle: {
                                color: '#71788C',
                            },
                        },
                        splitLine: {
                            show: true, //网格线
                            lineStyle: {
                                color: ['rgb(46,80,149, 0.3)'],
                                width: 1,
                                type: 'solid',
                            },
                        },
                    },
                ],
                seriesData: [
                    {
                        name: '成本',
                        type: 'bar',
                        barWidth: 20,
                        data: [100, 90, 80, 70, 60, 50, 40, 30, 20, 10],
                    },
                ],
            },
        }
    },
    components: {
        Chart,
    },
    mounted() {
        this.timeFn()
    },
    beforeDestroy() {
        clearInterval(this.timing)
    },
    methods: {
        timeFn() {
            this.timing = setInterval(() => {
                let tmp1 = Math.ceil(Math.random() * 10) - 1
                if (tmp1 >= 0) {
                    let cdata = JSON.parse(JSON.stringify(this.cdata))
                    let num = cdata.seriesData[0].data[tmp1]
                    num = num + 5
                    cdata.seriesData[0].data[tmp1] = num
                    this.cdata = cdata
                }
            }, 2000)
        },
    },
}
</script>

<style lang="scss" scoped></style>
