<template>
    <Echart :options="options" :height="cdata.height" :width="cdata.width"></Echart>
</template>

<script>
import Echart from '@/common/echart'
export default {
    data() {
        return {
            options: {},
        }
    },
    components: {
        Echart,
    },
    props: {
        cdata: {
            type: Object,
            default: () => ({}),
        },
    },
    watch: {
        cdata: {
            handler(newData) {
                const grid = newData.grid || {
                    top: '15%',
                    right: '10%',
                    left: '15%',
                    bottom: '20%',
                }
                this.options = {
                    color: newData.colors || ['#29ABE3', '#EECF1C'],
                    grid,
                    legend: {
                        show: newData.legendShow,
                        bottom: '3%',
                        data: newData.legendData,
                    },
                    xAxis: [
                        {
                            type: 'category',
                            axisTick: {
                                alignWithLabel: true,
                            },
                            spiltLine: {
                                show: true,
                            },
                            axisLabel: {
                                show: true,
                                textStyle: {
                                    color: '#71788C',
                                },
                            },
                            // prettier-ignore
                            data:newData.xAxisData
                        },
                    ],
                    yAxis: newData.yAxisData,
                    series: newData.seriesData,
                }
            },
            immediate: true,
            deep: true,
        },
    },
}
</script>

<style lang="scss" scoped></style>
